import React from "react";
import Box from "@material-ui/core/Box";
import { Typography, makeStyles, Grid } from "@material-ui/core";
import Persons from '../components/ui/Persons'
import personService from '../components/services/persons';

const useStyles = makeStyles((theme) => ({
    form: {
      textAlign: 'center',
    }
    
  }));

interface OwnProps {
  persons: any[];
}

type Props = OwnProps;


function ShowContacts({ persons }: Props) {

    const deletePerson = id => {
        personService
          .remove(id)
          .then(() => {
            const updatedPersons = persons.filter(p => p.id !== id);
            setPersons(updatedPersons);
          })
      }
  
  const classes = useStyles();
 
  return (
    <div>
      <Box className={classes.form}>
        <Typography variant="h5" color="primary">
          Your Contacts
        </Typography>
        <Box>
        <List>
          <Box>
            <Typography
              variant="h5"
              className={classes.title}
              style={{ color: "#575757" }}
            >
              My Contacts
            </Typography>
          </Box>
          <Box style={{display:'flex', justifyContent:'flex-end'}}>
            <Filter onChangeHandler={handleSearchName} />
          </Box>
            <Box className={classes.contacts}>
              <Grid item={true}>
                <Typography variant={"h6"}>
                  <Persons persons={contactsToShow} onDelete={deletePerson} />
                </Typography>
              </Grid>
           </Box>
          </List>
           </Box>
      </Box>
    </div>
  );
}

export default ShowContacts;
